.frame {
  $control-size: 1.5rem;

  position: relative;
  width: 100%;
  height: 100%;

  background-image: url(../../assets/img/transparency.png);
  background-size: cover;

  cursor: pointer;

  &.only-one {
    .control-frame {
      &.control-frame-delete,
      &.control-frame-move {
        display: none;
      }
    }
  }

  &:hover {
    .control-frame {
      display: block;
    }
  }

  &::before {
    content: attr(data-order-number);
    display: flex;
    width: $control-size;
    height: $control-size;
    justify-content: center;
    align-items: center;

    position: absolute;
    top: 0;
    left: 0;

    background-color: #ffd700;
  }

  .frame-canvas {
    width: 100%;
    height: 100%;
  }



  .control-frame {
    display: none;
    position: absolute;
    width: $control-size;
    height: $control-size;

    border: none;

    background-repeat: no-repeat;
    background-size: 70%;
    background-position: center;
    background-color: rgba(100, 100, 100, 0.6);
  
    &-delete {
      top: 0;
      right: 0;

      cursor: pointer;

      background-image: url(../../assets/img/icon_trash_bin.png);
    }

    &-dublicate {
      bottom: 0;
      right: 0;

      cursor: pointer;

      background-image: url(../../assets/img/icon_copy.png);
    }

    &-move {
      bottom: 0;
      left: 0;

      cursor: move;

      background-image: url(../../assets/img/icon_move.png);
    }
  }
}
