.header {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  height: 2rem;
  padding: 0 0.5rem;
  margin-bottom: 0.5rem;

  background: #888888;
}

.header__logo {
  margin: 0;
  padding: 0;

  font-size: 1.5rem;
  color: #ffffff;
}

.header__piskel-title {
  flex-grow: 1;
  margin: 0;

  font-weight: bold;
  font-size: 1rem;
  text-align: center;
  font-style: italic;
  color: darkorange;
}
