.color-control-block {
  position: relative;

  height: 5rem;
  margin-top: 1rem;

  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;

  .color-icon {
    $size: 2.5rem;

    position: absolute;
    width: $size;
    height: $size;

    border: 0.25rem solid #3a3a3a;

    &.transparent {
      background-image: url(../../assets/img/transparency.png);
      background-repeat: no-repeat;
      background-size: cover;
    }

    .hidden {
      display: none;
    }

    &.primary-color {
      transform: translate(-25%, -25%);
      z-index: 2;
    }

    &.secondary-color {
      transform: translate(25%, 25%);
    }
  }
}
