.preview-frames-block {
  $frame-size: 5rem;

  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 1rem 0.5rem;

  .preview-frames-list {
    $control-size: 1.5rem;
  
    display: flex;
    flex-direction: column;
    padding: 0;
    margin: 0;

    list-style: none;

    counter-reset: frame;
  
    .preview-frames-list__frame-list-item {
      position: relative;
      padding: 0;
      margin: 0;

      width: $frame-size;
      height: $frame-size;
  
      border: 0.2rem solid #444444;

      &:hover {
        border-color: #888888;
      }

      &.active {
        border-color: #ffd700;
      }
  
      & + .preview-frames-list__frame-list-item {
        margin-top: 0.5rem;
      }
    }
  }

  .add-new-frame-button {
    position: relative;
    display: flex;
    align-items: center;
    height: 3rem;
    margin-top: 1rem;
  
    border: 0.2rem solid #888888;
    border-radius: 0.2rem;

    background-color: #222222;
    color: #888888;

    cursor: pointer;

    &:hover {
      border-color: #ffff00;
    }

    .add-new-frame-button__plus-icon {
      display: inline-block;
      width: 1rem;
      height: 1rem;
      margin-right: 0.3rem;

      background-image: url(../../assets/img/icon_plus.png);
      background-repeat: no-repeat;
      background-size: contain;
    }

    .add-new-frame-button__text {
      display: inline-block;
      width: 3rem;

      font-size: 0.8rem;
    }
  }
}
