.preview-block {
  flex-basis:  20rem;
  padding: 0 0.5rem;

  box-sizing: border-box;

  .animated-canvas-block {
    position: relative;
    width: 100%;
    padding-bottom: 100%;
    margin-bottom: 1rem;

    background-color: #525253;
  }

  .animation-preview-canvas {
    position: absolute; 
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
  .fps-control-block {
    display: flex;
    justify-content: flex-start;
    padding: 0 0.5rem;
  
    .fps-control-text {
      font-size: 1.4rem;
      margin-right: 1rem;

      color: #aaaaaa;

      .fps-value {
        display: inline-block;
        width: 2rem;
        
        text-align: right;
      }
    }
  }
}
