.lools-list {
  $tool-size: 2rem;
  $tool-margin: 0.05rem;
  
  width: 2 * $tool-size  + $tool-margin * 4; 

  padding: 0;
  margin: 0 auto;
  margin-top: 0.5rem;

  list-style: none;
  font-size: 0;

  .lools-list__tool {
    position: relative;
    display: inline-block;
    width: $tool-size;
    height: $tool-size;
  
    margin: $tool-margin;
  
    background-color: #525252;
    background-size: 70%;
    background-repeat: no-repeat;
    background-position: center;

    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;

      display: block;
      width: 100%;
      height: 100%;
      box-sizing: border-box;
    }

    &:hover {
      background-color: lighten(#525252, 10%);
      cursor: pointer;
    }

    &.active {
      &::before {
        border: 0.1rem solid #ffd700;
      }
    }

    &-bucket {
      background-image: url(../../assets/img/icon_bucket.png);
    }

    &-eraser {
      background-image: url(../../assets/img/icon_eraser.png);
    }

    &-pen {
      background-image: url(../../assets/img/icon_pen.png);
    }

    &-color-picker {
      background-image: url(../../assets/img/icon_color_pick.png);
    }
  }
}


