.pixel-size-container {
  padding: 0;
  margin: 0;
  list-style: none;
  display: flex;
  justify-content: center;

  .pixel-size-container__pixel-size-variant {
    display: inline-block;
    width: 1.5rem;
    height: 1.5rem;
    box-sizing: border-box;
  
    border: 2px solid#444;
    cursor: pointer;

    &.active {
      border-color: #ffd700;
    }
  
    &[data-size='1'] {
      padding: 0.4rem;
    }
    
    &[data-size='2'] {
      padding: 0.3rem;
    }

    &[data-size='3'] {
      padding: 0.2rem;
    }

    &[data-size='4'] {
      padding: 0.1rem;
    }

    
    &::before {
      content: '';
        width: 100%;
        height: 100%;
        background-color: white;
        display: block;
    }
    
    & + .pixel-size-container__pixel-size-variant {
      margin-left: 0.05rem;
    }
  }
}


