.piskel-block {
  flex-grow: 1;

  display: flex;
  justify-content: center;
  align-items: center;

  background-color: #525253;

  $piskel-size: 20rem;

  .square-container {
    position: relative;
    width: $piskel-size;
    height: $piskel-size;

    background-image: url('../../assets/img/transparency.png');
    background-size: cover;

    &.pen-mode {
      cursor: url(../../assets/img/cursor_icon_pen.png), auto;
    }

    &.bucket-mode {
      cursor: url(../../assets/img/cursor_icon_bucket.png), auto;
    }

    &.eraser-mode {
      cursor: url(../../assets/img/cursor_icon_eraser.png), auto;
    }

    &.colorPicker-mode {
      cursor: url(../../assets/img/cursor_icon_color_pick.png), auto;
    }

    .piskel-canvas {
      width: 100%;
      height: 100%;
    }
  }
}
